// X·ª≠ l√Ω signout

import { auth, provider, db, signInWithPopup } from "@/src/firebaseConfig";
import { doc, setDoc, getDoc, serverTimestamp } from "firebase/firestore";
import { signOut } from "firebase/auth";

const signOutUser = async () => {
  try {
    await signOut(auth);
    console.log("‚úÖ Signed out successfully");
    window.location.href = "/"; // ho·∫∑c d√πng router.push("/") n·∫øu d√πng Next.js router
  } catch (error) {
    console.error("‚ùå Error signing out:", error);
  }
};

const signInWithGoogle = async () => {
  try {
    // Hi·ªÉn th·ªã popup ƒëƒÉng nh·∫≠p Google
    const result = await signInWithPopup(auth, provider);
    const user = result.user;

    // T·∫°o document tham chi·∫øu t·ªõi Firestore
    const userRef = doc(db, "users", user.uid);
    const userSnap = await getDoc(userRef);

    // N·∫øu user ch∆∞a t·ªìn t·∫°i trong Firestore, th√™m m·ªõi
    if (!userSnap.exists()) {
      await setDoc(userRef, {
        uid: user.uid,
        name: user.displayName || "",
        email: user.email || "",
        photoURL: user.photoURL || "",
        role: "Customer", // üëà G√°n role m·∫∑c ƒë·ªãnh
        createdAt: serverTimestamp(), // ‚è± D√πng timestamp chu·∫©n
      });
    }

    console.log("‚úÖ User signed in:", user);
    return user; // Tr·∫£ v·ªÅ user ƒë·ªÉ d√πng trong ·ª©ng d·ª•ng

  } catch (error: any) {
    if (error.code === "auth/popup-closed-by-user") {
      console.warn("‚ö†Ô∏è Ng∆∞·ªùi d√πng ƒë√£ ƒë√≥ng c·ª≠a s·ªï ƒëƒÉng nh·∫≠p tr∆∞·ªõc khi ho√†n t·∫•t.");
    } else {
      console.error("‚ùå L·ªói khi ƒëƒÉng nh·∫≠p:", error);
    }
    return null;
  }
};

export { signInWithGoogle, signOutUser };
